.paginationBttns {
  width: 80%;
  height: 40px;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.paginationBttns a {
  padding: 10px;
  margin: 14px;
  font-weight: 600;
  font-size: 18px;
  border-radius: 5px;
  border: 2px solid teal;
  color: teal;
  cursor: pointer;
  transition: 0.1s all ease;
}

.paginationBttns a:hover {
  color: white;
  background-color: teal;
}

.paginationActive a {
  color: white;
  background-color: teal;
}
