:root {
  --swiper-theme-color: white;
}
.swiper-button-next,
.swiper-button-prev {
  /* required styles */
  width: 60px;
  height: 60px;
  background-color: rgba(255, 255, 255, 0.5);
  /* text-align: center; */
  font-size: 50px;
  font-weight: 500;
  border-radius: 50%;
  cursor: pointer;
  color: black;
  margin-top: -40px;
}

.swiper-button-next {
  padding-left: 5px;
}
.swiper-button-prev {
  padding-right: 3px;
}
